.card {
  color: #060b35;
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 16px;
  font-family: Roboto, Arial, sans-serif;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 16px 3px 40px;
}

.card__description {
  flex-direction: column;
  gap: 4px;
  margin-bottom: 16px;
  display: flex;
}

.card__description-name {
  font-size: 12px;
  font-weight: 500;
  line-height: 150%;
}

.card__description-code {
  color: #616070;
  font-size: 10px;
  line-height: 140%;
}

.card__rate {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__rate-reviews {
  font-size: 10px;
  line-height: 140%;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  line-height: 150%;
}

.card__price-value {
  font-size: 16px;
  font-weight: 700;
  line-height: 112.5%;
}

.card__button {
  box-sizing: border-box;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
